/** SPACING */
#header {
  font-family: "Montserrat", sans-serif;
  height: 55px;
  z-index: 3;
  background-color: #5CBBBF;
  width: 100%;
  position: absolute;
  box-shadow: 0 0 6px 2px #999999;
}
#header .logo {
  background-image: url("../../../assets/rethink-logo.png");
  background-size: cover;
  height: 55px;
  width: 140px;
  position: absolute;
  z-index: 4;
}
#header #google-buttons {
  position: absolute;
  right: 0px;
  width: 10%;
  max-width: 120px;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
}
#header #google-buttons button {
  padding: 3% 3% 5% 3%;
  width: 60px;
  font-family: inherit;
  color: white;
  font-size: 9.8887515451px;
  border: 1px solid rgba(255, 255, 255, 0.562);
  background-color: #5CBBBF;
  border-radius: 5%;
  cursor: pointer;
}
#header #google-buttons button:hover {
  transform: scale(1.05);
  background-color: #62cace;
  transition: transform 100ms;
}
#header #google-buttons button.hide {
  display: none;
}