#list {
  font-family: "Montserrat", sans-serif;
  position: absolute;
  text-align: left;
  top: 10%;
  left: 25px;
  width: 20%;
  min-width: 250px;
  height: 85%;
  background-color: #FEFFFF;
  overflow: scroll;
  white-space: nowrap;
  box-shadow: 0 0 8px 1px #ccc;
  border-radius: 3px;
  z-index: 3;
  display: flex;
  flex-direction: column;
}

.wrapper {
  max-height: 30px;
  min-height: 30px;
  overflow: hidden;
  transition: max-height 300ms;
}
.wrapper .category::before {
  transform: translateY(-15%) translateX(-20%) rotate(-90deg);
}
.wrapper.isOpen {
  max-height: 1000px;
}
.wrapper.isOpen .category::before {
  transform: translateY(-15%) translateX(0%);
}
.wrapper.isOpen .category .name {
  font-weight: 600;
}
.wrapper .category {
  padding: 0.5em;
  border-bottom: #EEE 1px solid;
  font-weight: 400;
  display: flex;
  align-items: center;
}
.wrapper .category::before {
  content: "";
  width: 30px;
  height: 100%;
  background-image: url("../../../assets/arrow.svg");
  transform-origin: center;
  transition: transform 200ms;
}
.wrapper .category .count {
  font-weight: 600;
}
.wrapper .body {
  padding: 0 30px 0 0;
  height: 100%;
  overflow: scroll;
}

.category, .listItem, .listItemRow {
  font-size: 9.8887515451px;
  box-sizing: border-box;
  width: 100%;
  overflow: hidden;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.listItem, .category {
  border-bottom: #EEE 1px solid;
}

.listItemIcon {
  width: 30px;
  padding: 5px;
}
.listItemIcon path.map-point, .listItemIcon path.circle {
  opacity: 0;
}

.inBuffer span {
  font-weight: 700;
}
.inBuffer path.map-point {
  opacity: 1;
  fill-opacity: 0;
  stroke-width: 1.5;
}
.inBuffer path.circle {
  opacity: 1;
  stroke-width: 1.5;
}

.selected span {
  font-weight: 700;
}
.selected path.map-point {
  opacity: 1;
  fill-opacity: 1;
  stroke-width: 0;
}
.selected path.circle {
  opacity: 1;
  stroke-width: 0;
}